<template>
  <div>

    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <!--        <form  autocomplete="off" @submit.prevent="formSubmit">-->
        <div class="common-main box">

          <!-- <van-cell title="项目名称" :value="wybmListItem.xmmc" /> -->
          <div class="page-list">
            <div class="pagelist-left">项目名称</div>
            <div class="pagelist-right">{{ wybmListItem.xmmc }}</div>
          </div>
          <div class="input-item hsicon">
            <van-field
              v-model="formData.username"
              v-validate="{'required': 'true', 'minChar': '2','maxChar':'20'}"
              :error-message="errors.first('username')"
              label="联系人姓名"
              required
              clearable
              name="username"
              placeholder="请输入联系人姓名"
              input-align="right"
            />
            <div class="item-icon" @click="getLxrList">
              <i class="iconfont iconicon-lxr" />
            </div>
            <!-- <p class="input-item-msg">{{ errors.first('username') }}</p> -->
          </div>

          <div class="input-item">
            <van-field
              ref="input4"
              v-model="formData.phone"
              v-validate="'required|phone'"
              required
              clearable
              :error-message="errors.first('phone')"
              label="联系人手机"
              name="phone"
              placeholder="请输入联系人手机"
              input-align="right"
            />
            <!-- <p class="input-item-msg">{{ errors.first('phone') }}</p> -->
          </div>
          <div class="input-item">
            <van-field
              ref="input4"
              v-model="formData.email"
              v-validate="'required|email'"
              name="email"
              :error-message="errors.first('email')"
              required
              clearable
              label="电子邮箱"
              placeholder="请输入电子邮箱"
              input-align="right"
            />
            <!-- <p class="input-item-msg">{{ errors.first('email') }}</p> -->
          </div>
          <!-- <div class="input-item">
              <van-field ref="input4" v-model="formData.bgdh" label="办公电话" placeholder="请输入办公电话" input-align="right" />
            </div>
            <div class="input-item">
              <van-field ref="input4" v-model="formData.cz" label="传真" placeholder="请输入传真" input-align="right" />
            </div> -->

        </div>
      </div>
      <!--      </form>-->
    </div>
    <!-- <div class="content">
        <div class="box-list box">
          <div class="input-item hsicon">
            <van-field v-model="username" label="上传附件" placeholder="添加图片" input-align="right" maxlength="" />
            <div class="item-icon">
              <i class="iconfont icontianjiatupian" />
            </div>
          </div> -->

    <!-- <van-field class="upload" title="上传附件" align="left" @click="onClick">
              <span slot="right" class="upload-icon">
                添加图片
                <i slot="right" class="iconfont icontianjiatupian" />
              </span>
            </van-field> -->
    <!-- </div>
      </div> -->
    <div class="foot-btn fixed">
      <ul>
        <li><van-button type="info" size="large" round @click="sendWybmAction('vanForm')">立即报名</van-button></li>
      </ul>
    </div>
  </div>

</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar'
import { sendWybmAction } from '@/store/gys/wybm'
import { mapState } from 'vuex'
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'GysXmbm',
  components: {
    headerBar
  },
  // mixins: [pageRouter],
  data() {
    return {
      headerData: {
        title: '项目报名',
        leftUrl: 'back',
        leftText: ' '
      },
      fbxx: '',
      // 表单数据源
      formData: {
        phone: '',
        email: '',
        bgdh: null,
        cz: null
      },
      // wybmListItem: null,
      username: '',
      phone: '',
      email: '',
      bgdh: '',
      cz: '',
      ids: ''
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      wybmListItem: state => state.gyswybm.wybmListItem,
      lxrItem: state => state.gyswybm.lxrItem
    })
  },
  created() {
    this.fbxx = this.$store.state.gyswybm.fbxx
    this.ids = this.$store.state.gyswybm.fbid
  },

  mounted() {
    if (this.lxrItem.length !== 0 && this.lxrItem !== '') {
      this.formData.username = this.lxrItem.lxr
      this.formData.phone = this.lxrItem.lxdh
      this.formData.email = this.lxrItem.email
    }
  },

  methods: {
    getLxrList() {
      console.log('lxr====', this.fbxx)
      if (this.fbxx === null || this.fbxx === '') {
        this.$toast.fail('暂无联系人')
      } else {
        this.$router.push({
          name: 'GysLxrxx',
          params: { fbxx: this.fbxx, ids: this.ids }
        })
      }
    },
    sendWybmAction(formName) {
      this.$validator.validate().then(result => {
        if (result) {
          sendWybmAction({
            zzid: this.userInfo.zzid,
            zzmc: this.userInfo.zzmc,
            ryid: this.userInfo.id,
            rymc: this.userInfo.name,
            xmid: this.wybmListItem.xmid,
            zffs: '2', // 1->网上支付 2->现金
            gyslxr: { 'lxr': this.formData.username, 'lxdh': this.formData.phone, 'email': this.formData.email },
            gysSelectFbid: this.ids
          }).then((res) => {
            // console.info(res)
            // this.res = res
            // console.info(res.gysyqk)
            this.$toast.success('报名成功')
            this.$router.push('/Wybm')
            // eslint-disable-next-line no-unused-vars
          })
        }
      })
    }

  }
}
</script>

<style lang="css" scoped>
/deep/ .van-field__error-message{text-align: right;}
 </style>
